import {useState } from 'react';
import { UserIcon, KeyIcon } from "@heroicons/react/outline"
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import { signin } from "../store/_actions/UserAction";

function SigninPage() {
    const [userId,setUserId]=useState('');
    const [password,setPassword]=useState('')
    const dispatch=useDispatch()

    return (
        <div id="SigninPage" className="flex flex-col h-screen items-center md:pt-20 md:px-12 bg-gray-200">
            <div id="SigninContainer" className="bg-white flex flex-col shadow-lg w-full h-full xl:w-1/3 md:w-1/2 md:h-3/5 rounded-b-2xl">
                {/* 상단 부분 */}
                <div className="flex justify-center items-center h-36 bg-blue-500 rounded-tl-lg rounded-br-full">
                    <h1 className="text-white text-4xl font-semibold">
                        회원 로그인
                    </h1>
                </div>

                {/* 중앙 Form 부분 */}
                <div className="flex flex-col space-y-6 md:space-y-8 px-2 pt-10 pb-10 items-center">
                    {/* 아이디 입력란 */}
                    <div className="flex items-center  p-1 w-2/3">
                        <UserIcon className="h-7 text-gray-400"/>
                        <input 
                            type="text"
                            className="bg-transparent outline-none text-md w-full pl-2
                                             placeholder-gray-400 ml-2 border-b-2 border-gray-200" 
                            value={userId}
                            onChange={(e)=>{setUserId(e.target.value)}}
                            placeholder="아이디를 입력하세요" />
                    </div>

                    {/* 패스워드 입력란 */}
                    <div className="flex items-center p-1 w-2/3">
                        <KeyIcon className="h-7 text-gray-400"/>
                        <input 
                            type="password"
                            className="bg-transparent outline-none text-md w-full pl-2
                                             placeholder-gray-400 ml-2 border-b-2 border-gray-200" 
                            value={password}
                            onChange={(e)=>{setPassword(e.target.value)}}
                            placeholder="패스워드를 입력하세요" />
                    </div>      

                    <button className="w-5/12 bg-blue-400 text-white rounded-full p-4 font-bold
                                                    hover:scale-105 transform transition duration-300 ease-in-out"
                        onClick={()=>{
                            if(!userId||!password){
                                alert('입력정보를 확인해주세요')
                            }else{
                                const data={
                                    userId,
                                    password
                                }
                                axios.post('https://chatbot-djangoserver.herokuapp.com/api/signin', data).then(res=>{
                                if(res.data.success===true){
                                    //로그인 처리
                                    localStorage.setItem('isLogin',true)
                                    localStorage.setItem('userId',res.data.userId)
                                    localStorage.setItem('name',res.data.name)
                                    dispatch(signin());
                                    window.location.href='#recommend';
                                }else if(res.data.type==='incorrectPW'){
                                    alert('패스워드를 확인하세요')
                                }else if(res.data.type==='noUser'){
                                    alert('존재하지 않는 아이디입니다')
                                }
                            })
                            }
                        }}
                    >
                            로그인
                    </button>
                </div>
            </div>
            
        </div>
    )
}

export default SigninPage
