import AccessibilityNewIcon from '@material-ui/icons/AccessibilityNew';
import navImg from '../images/navImg.png';
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { signout } from "../store/_actions/UserAction";

function Navbar() {
  const loginState=useSelector(state=>state.user.isLogin);
  const dispatch=useDispatch();
  
  return (
    <div className='sticky top-0 z-50 md:h-16 bg-yellow-100 flex items-center p-2 lg:px-5 font-bold text-xl shadow-md justify-between'>
      {/* 좌측 */}
      <div className='flex items-center'>
        <div className="flex items-center">
          <img src={navImg} alt={navImg} className='w-10 h-10 ' />
          <Link to='/'>
          <p className='text-gray-800 ml-2'>모두의 복지</p>
        </Link>

      {loginState && (
        <div>
          <Link to='/recommend'>
            <p className="text-sm text-gray-600 ml-2 md:ml-4 cursor-pointer">추천정보</p>
          </Link>
        </div>
      )}
      </div>      
        </div>

      {loginState?(
        <div className="flex items-center space-x-2 md:space-x-4">
          <p className="text-sm md:text-base">{localStorage.getItem('name')}님</p>
          <p className="text-xs md:text-sm text-red-500 cursor-pointer" 
               onClick={()=>{
                 dispatch(signout());
                 localStorage.removeItem('isLogin');
                 localStorage.removeItem('userId');
                 localStorage.removeItem('name');
                 window.location.href='#';
               }}>로그아웃</p>
    </div>
      ):(
        <div className="hidden items-center space-x-2 md:space-x-4">
          <Link to="/Signin">
            <p className="text-xs md:text-sm text-gray-500 cursor-pointer" >로그인</p>
          </Link>
          <Link to="/Signup">
            <p className="text-xs md:test-sm text-gray-500 cursor-pointer" >회원가입</p>
          </Link>
        </div>
      )}
    </div>
  )
}

export default Navbar;