import {useState} from 'react';
import Carousel from 'nuka-carousel';
import CircularProgress from '@material-ui/core/CircularProgress';
import { ChevronRightIcon, ChevronLeftIcon } from '@heroicons/react/solid'
import { resetDialog } from "../store/_actions/DialogAction";
import { useDispatch } from 'react-redux';
import botImg from '../images/bot2.png';

function MessageObj({ type, msg = '', data = [] }) {
    const dispatch = useDispatch();
    const dataCount=data?.length;

    let justify = '';
    if (type === 'bot' || type === 'data') {
        justify = 'flex justify-start mx-3  md:mx-4 md:w-5/12 w-3/5';
    }
    else {
        justify = 'flex mx-2 md:mx-4 justify-end';
    }

    return (
        <div className={justify}>
            {type === 'bot' && (<div className='flex items-center mb-8'>
                {/* 봇 이미지 */}
                <div className='flex-shrink-0'>
                    <svg className='md:w-12 md:h-12 w-8 h-8 text-green-700 fill-current' role="img" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="M11.999 0c-2.25 0-4.5.06-6.6.21a5.57 5.57 0 00-5.19 5.1c-.24 3.21-.27 6.39-.06 9.6a5.644 5.644 0 005.7 5.19h3.15v-3.9h-3.15c-.93.03-1.74-.63-1.83-1.56-.18-3-.15-6 .06-9 .06-.84.72-1.47 1.56-1.53 2.04-.15 4.2-.21 6.36-.21s4.32.09 6.36.18c.81.06 1.5.69 1.56 1.53.24 3 .24 6 .06 9-.12.93-.9 1.62-1.83 1.59h-3.15l-6 3.9V24l6-3.9h3.15c2.97.03 5.46-2.25 5.7-5.19.21-3.18.18-6.39-.03-9.57a5.57 5.57 0 00-5.19-5.1c-2.13-.18-4.38-.24-6.63-.24zm-5.04 8.76c-.36 0-.66.3-.66.66v2.34c0 .33.18.63.48.78 1.62.78 3.42 1.2 5.22 1.26 1.8-.06 3.6-.48 5.22-1.26.3-.15.48-.45.48-.78V9.42c0-.09-.03-.15-.09-.21a.648.648 0 00-.87-.36c-1.5.66-3.12 1.02-4.77 1.05-1.65-.03-3.27-.42-4.77-1.08a.566.566 0 00-.24-.06z"/></svg>
                </div>

                {/* 봇 메시지 */}
                <div className='flex bg-gray-100 rounded-xl ml-2 p-4 hover:scale-105 
                    transition duration-700 transform ease-in-out shadow-md'>
                    {msg ? (<p>{msg}</p>) : (
                        <CircularProgress size={30} />
                    )}
                </div>
            </div>)}

            {type === 'user' && (<div className='flex justify-end items-center mb-8 md:w-5/12'>
                {/*사용자 메시지 */}
                <div className=' flex bg-blue-400 rounded-xl ml-2 p-4 text-white shadow-md'>
                    <p>{msg}</p>
                </div>
            </div>)}

            {data.length>0 && (
                <div className='flex flex-col w-full items-center'>
                    <Carousel className='flex px-4 py-4 md:px-7 ml-2 md:py-8 bg-blue-50 md:hover:scale-105 rounded-xl 
                                    font-semibold transform duration-500 shadow-lg mb-4 outline-none' wrapAround={true}
                        defaultControlsConfig={{
                            pagingDotsStyle: {
                                fill: "purple",
                                margin: '3px'
                            }
                        }}
                        renderCenterLeftControls={({ previousSlide }) => (
                            <ChevronLeftIcon className='w-8 h-8 opacity-50 hover:text-gray-500 animate-bounce text-gray-400' onClick={previousSlide} />
                        )}
                        renderCenterRightControls={({ nextSlide }) => (
                            <ChevronRightIcon className='w-8 h-8 opacity-50 hover:text-gray-500 animate-bounce text-gray-400' onClick={nextSlide} />
                        )}>
                        {data.map(d => (
                            <div className='flex flex-col items-center justify-center' key={d.title}
                                onClick={() => { window.open(`${d.link}`, '_blank') }}>
                                <p className='text-sm text-green-700'>[{d.location}{d.location_detail??' '.concat(d.location_detail)}]</p>
                                <p className='text-blue-700 text-sm md:text-lg md:mb-4 mb-2'>{d.title}</p>
                                <p className='md:text-sm md:pb-0 pb-2 font-semibold'>{d.description}</p>
                            </div>
                        ))}
                    </Carousel>
                    <div className="flex items-center justify-center space-x-3">
                        {dataCount>10&&(
                            <button
                            className=' bg-green-400 text-white text-sm p-2 rounded-full
                                            animate-pulse float-right md:ml-2 mb-8'
                            onClick={() => {
                                            
                                        }}
                        >다른 결과보기</button>
                        )}
                        <button
                            className=' bg-blue-400 text-white text-sm p-2 rounded-full
                                            animate-pulse float-right md:ml-2 mb-8'
                            onClick={() => {
                                dispatch(resetDialog());
                            }}
                        >다시 대화하기</button>
                    </div>
                </div>
            )}
        </div>
    )
}

export default MessageObj;
