import {useState, useEffect,useRef} from 'react'
import Fade from '@mui/material/Fade'
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
const testData=[
    {
        title:'복지',
        location:'경기도',
        location_detail:'수원시',
        desc:'복지 설명'
    },
    {
        title:'복지2',
        location:'경기도',
        location_detail:'양주시',
        desc:'복지 설명'
    },
    {
        title:'복지3',
        location:'서울특별시',
        location_detail:'서대문구',
        desc:'서울특별시 서대문구 연희동에 거주중인 20대 모두에게 월 200만원씩 12개월간 용돈 지급. '
    },
    {
        title:'복지4',
        location:'부산광역시',
        location_detail:'해운대구',
        desc:'주거 지원'
    },
    {
        title:'복지5',
        location:'경기도',
        location_detail:'의정부시',
        desc:'복지 복지'
    },
    {
        title:'서울 청년 희망의 집',
        location:'서울특별시',
        location_detail:'서대문구',
        desc:'서울시 서대문구에 거주하는 청년에게 희망을 주기 위해 30평 오피스텔 지급'
    },
    {
        title:'서울 청년 희망의 집',
        location:'서울특별시',
        location_detail:'서대문구',
        desc:'복지 설명'
    },
    {
        title:'서울 청년 희망의 집',
        location:'서울특별시',
        location_detail:'서대문구',
        desc:'설명'
    }
]

function RecommendPage() {
    const [firstFade, setFirstFade]=useState(false);
    const [secondFade, setSecondFade]=useState(false);
    const [thirdFade, setThirdFade]=useState(false);
    const [fourthFade, setFourthFade]=useState(false);

    const firstSliderRef=useRef(null);
    const secondSliderRef=useRef(null);
    const thirdSliderRef=useRef(null);

    useEffect(()=>{
        setTimeout(()=>{
            setFirstFade(true);
            setTimeout(()=>{
                setSecondFade(true);
                setTimeout(()=>{
                    setThirdFade(true);
                    setTimeout(()=>{
                        setFourthFade(true)
                    },500)
                },500)
            },500)
        },200);

    },[])
    return (
        <div className="flex flex-col h-screen overflow-y-auto px-10 pt-12 pb-20 space-y-8 shadow-xl bg-white">
            <Fade in={firstFade} timeout={1000}>
            <p className='text-3xl font-bold '>{localStorage.getItem('name')}님이 관심가지실 만한 복지 정보들을 가져와 봤어요!</p>
            </Fade>
            {/* 사용자 클릭 기반 추천 정보 */}
            <Fade in={secondFade} timeout={1000}>
                <div className="px-8">
                    <p className='text-md text-gray-700 font-semibold'>{localStorage.getItem('name')}님이 찾아보셨던 복지와 비슷한 정보</p>
                    <div className='flex relative'>
                        {/* 좌우 스크롤 아이콘 */}
                        <div className="absolute top-1/3 md:left-1 left-3 z-10 cursor-pointer">
                            <ArrowBackIosIcon fontSize="large" className="absolute" onClick={()=>{
                                firstSliderRef.current.scrollLeft-=400;
                            }} />
                        </div>
                        <div className="absolute top-1/3 md:right-1 right-6 z-10 cursor-pointer">
                            <ArrowForwardIosIcon fontSize="large" onClick={()=>{
                                firstSliderRef.current.scrollLeft+=400;
                            }} />
                        </div>
                        <div className="flex w-80 md:w-full overflow-x-scroll rounded-2xl shadow-md px-3 py-2 space-x-3 bg-gray-100" ref={firstSliderRef}>
                        {testData.map(d=>(
                        <div className='flex flex-col flex-shrink-0 w-full md:w-80 bg-white shadow-md rounded-xl hover:scale-105 transform 
                                                transition duration-200 ease-in-out  px-4 py-8 items-center' key={d.title}>
                            <p className='text-sm font-semibold text-green-700'>[{d.location} {d.location_detail}]</p>
                            <p className='text-blue-700 font-bold text-sm md:text-lg md:mb-4 mb-2'>{d.title}</p>
                            <p className='md:text-sm md:pb-0 pb-2 font-semibold'>{d.desc}</p>
                        </div>
                        ))}
                        </div>
                    </div>
                </div>
            </Fade>

            {/* 사용자 연령대, 거주지 기반 추천 정보 */}
            <Fade in={thirdFade} timeout={1000}>
                <div className="px-8">
                    <p className='text-md text-gray-700 font-semibold'>{localStorage.getItem('name')}님이 찾아보셨던 복지와 비슷한 정보</p>
                    <div className='flex relative'>
                        {/* 좌우 스크롤 아이콘 */}
                        <div className="absolute top-1/3 left-1 z-10 cursor-pointer">
                            <ArrowBackIosIcon fontSize="large" className="absolute" onClick={()=>{
                                secondSliderRef.current.scrollLeft-=400;
                            }} />
                        </div>
                        <div className="absolute top-1/3 right-1 z-10 cursor-pointer">
                            <ArrowForwardIosIcon fontSize="large" onClick={()=>{
                                secondSliderRef.current.scrollLeft+=400;
                            }} />
                        </div>
                        <div className="flex w-80 md:w-full overflow-x-scroll rounded-2xl shadow-md px-3 py-2 space-x-3 bg-gray-100" ref={secondSliderRef}>
                        {testData.map(d=>(
                        <div className='flex flex-col flex-shrink-0 w-full md:w-80 bg-white shadow-md rounded-xl hover:scale-105 transform 
                                                transition duration-200 ease-in-out  px-4 py-8 items-center' key={d.title}>
                            <p className='text-sm font-semibold text-green-700'>[{d.location} {d.location_detail}]</p>
                            <p className='text-blue-700 font-bold text-sm md:text-lg md:mb-4 mb-2'>{d.title}</p>
                            <p className='md:text-sm md:pb-0 pb-2 font-semibold'>{d.desc}</p>
                        </div>
                        ))}
                        </div>
                    </div>
                </div>
            </Fade>

            {/* 사용자가 담아둔 복지 정보 */}
            <Fade in={fourthFade} timeout={1000}>
                <div className="px-8">
                    <p className='text-md text-gray-700 font-semibold'>{localStorage.getItem('name')}님이 찾아보셨던 복지와 비슷한 정보</p>
                    <div className='flex relative'>
                        {/* 좌우 스크롤 아이콘 */}
                        <div className="absolute top-1/3 left-1 z-10 cursor-pointer">
                            <ArrowBackIosIcon fontSize="large" className="absolute" onClick={()=>{
                                thirdSliderRef.current.scrollLeft-=400;
                            }} />
                        </div>
                        <div className="absolute top-1/3 right-1 z-10 cursor-pointer">
                            <ArrowForwardIosIcon fontSize="large" onClick={()=>{
                                thirdSliderRef.current.scrollLeft+=400;
                            }} />
                        </div>
                        <div className="flex w-80 md:w-full overflow-x-scroll rounded-2xl shadow-md px-3 py-2 space-x-3 bg-gray-100" ref={thirdSliderRef}>
                        {testData.map(d=>(
                        <div className='flex flex-col flex-shrink-0 w-full md:w-80 bg-white shadow-md rounded-xl hover:scale-105 transform 
                                                transition duration-200 ease-in-out  px-4 py-8 items-center' key={d.title}>
                            <p className='text-sm font-semibold text-green-700'>[{d.location} {d.location_detail}]</p>
                            <p className='text-blue-700 font-bold text-sm md:text-lg md:mb-4 mb-2'>{d.title}</p>
                            <p className='md:text-sm md:pb-0 pb-2 font-semibold'>{d.desc}</p>
                        </div>
                        ))}
                        </div>
                    </div>
                </div>
            </Fade>
        </div>
    )
}

export default RecommendPage;
