import { SIGNIN, SIGNOUT } from '../_actions/ActionTypes'

const initialState={
    isLogin:false
}

export default function userReducer(state=initialState.isLogin, action){
    switch (action.type){
        case SIGNIN:
            return {isLogin:true}

        case SIGNOUT:
            return {isLogin:false}
        default:
            return state
    }
}